import { createClient } from 'microcms-js-sdk'
import { useEffect, useState } from 'react'
import { Microcms, MicrocmsContents } from 'type/Microcms'

export const microcms = createClient({
  serviceDomain: 'kazuki-lv12',
  apiKey: process.env.REACT_APP_MICROCMS_API_KEY || '',
})

/**
 *  blog 
 * -  blog 
 * - tag  blog 
 */
export const useGetBlogList = (tag: string | null) => {
  const [total, setTotal] = useState<number>(0)
  const [blogList, setBlogList] = useState<Array<MicrocmsContents>>([])
  const [loading, setLoading] = useState<boolean>(true)

  useEffect(() => {
    try {
      ; (async () => {
        if (tag) {
          const res = (await microcms.get({
            endpoint: 'blogs',
            queries: { filters: `tag[contains]${tag}` },
          })) as Microcms

          setBlogList(res.contents)
          setTotal(res.totalCount)
          setLoading(false)
        } else {
          const res = (await microcms.get({
            endpoint: 'blogs',
          })) as Microcms

          setBlogList(res.contents)
          setTotal(res.totalCount)
          setLoading(false)
        }
      })()
    } catch {
      setBlogList([])
      setLoading(false)
    }
  }, [tag])

  return {
    blogList,
    total,
    loading,
  }
}

/**
 *  blog 
 * - id  blog 
 */
export const useGetBlog = (id: string | null | undefined) => {
  const [blog, setBlog] = useState<MicrocmsContents | null>(null)
  const [loading, setLoading] = useState<boolean>(true)

  useEffect(() => {
    try {
      ; (async () => {
        const res = (await microcms.get({
          endpoint: 'blogs',
          queries: { ids: `${id}` },
        })) as Microcms

        setBlog(res.contents[0])
        setLoading(false)
      })()
    } catch {
      setBlog(null)
      setLoading(false)
    }
  }, [id])

  return { blog, loading }
}

type Tag = {
  [tag: string]: number;
}

type Tags = Array<Tag>

export const useGetTags = () => {
  const [tags, setTags] = useState<Tags>([])
  const [loading, setLoading] = useState<boolean>(true)

  useEffect(() => {
    try {
      ; (async () => {
        const res = (await microcms.get({
          endpoint: 'blogs',
          queries: { fields: 'tag', limit: 10000 }
        })) as Microcms

        // 
        const count: Tag = {};

        for (var i = 0; i < res.contents.length; i++) {
          var tag = res.contents[i].tag[0];

          if (Object.keys(count).indexOf(tag) === -1) {
            count[tag] = 1;
          } else {
            count[tag] = count[tag] + 1;
          }
        }

        // 
        const tags: Tags = []

        for (const [key, value] of Object.entries(count)) {
          const tag: Tag = {}
          tag[key] = value

          tags.push(tag)
        }

        setTags(tags)
        setLoading(false)
      })()
    } catch {
      setTags([])
      setLoading(false)
    }
  }, [])

  return {
    tags,
    loading
  }
}